<!-- 新闻动态 -->
<template>
  <div class="news">
    <div
      class="news-list width"
      v-for="(item,i) in newsList"
      :key="i"
      @click="gotoDetails(item.id)"
    >
      <div class="news-img">
        <img :src="item.cover_photo" alt srcset />
      </div>
      <div class="news-content">
        <div class="news-content-title">{{item.title}}</div>
        <div class="news-content-con">{{item.briefi_content}}</div>
      </div>
      <div class="news-time">{{item.create_time | formatDate}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  components: {},
  props: {},
  data() {
    return {
      newsList: [],
    }
  },
  computed: {},
  watch: {},
  created() {
    this.getNewsList()
  },
  mounted() {},
  methods: {
    async getNewsList() {
      const res = await this.$http.post('/getIndexNews')
      console.log(res)
      this.newsList = res.data.data
    },
    // 点击跳转到详情页
    gotoDetails(i) {
      this.$router.push({
        path: '/newsdetails',
        query: {
          //通过query 传递参数
          goodsDetail: i,
        },
      })
    },
  },
  // filters: {
  //   formatDate(time) {
  //     time = time * 1000
  //     let date = new Date(time)
  //     console.log(new Date(time))
  //     return formatDate(date, 'yyyy-MM-dd hh:mm')
  //   },
  // },
}
</script>

<style lang="less" scoped>
.news {
  .news-list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    border-bottom: 1px solid #fff;
    padding-bottom: 10px;
    cursor: pointer;
  }
}
.news-img {
  flex: 1;
  margin-right: 30px;
  img {
    width: 100%;
  }
}
.news-content {
  flex: 4;
  .news-content-title {
    color: #fff;
    font-weight: 700;
    font-style: normal;
    font-size: 16px;
    margin-bottom: 26px;
  }
  .news-content-con {
    color: #fff;
    line-height: 18px;
  }
}

.news-time {
  flex: 1;
  color: #fff;
  text-align: right;
  font-weight: 700;
  font-size: 16px;
  color: #32a8ff;
  max-width: 100px;
}
</style>
