<template>
  <div class="header">
    <div class="menu clearfix">
      <div class="menu-logo">
        <el-avatar :size="40" :src="imgLogo"></el-avatar>
        <span>织立方工业互联网平台</span>
      </div>
      <div class="menu-list" v-for="(item,i) in tableData" :key="i">
        <div
          class="menu-title"
          :class="index==i?'act':''"
          @mouseover="getTwoList(item.id,item.name,i)"
          @click="gotoLink(item.url,item.id,i)"
        >{{item.name}}</div>
        <!-- <div class="menu-item" v-if="item.children!=undefined && item.children.length!=0"> -->

        <div class="menu-item" v-show="show" @mouseover="show=true" @mouseout="show=false">
          <div class="menu-item-menu">
            <div class="menu-item-menu-title">{{twoName}}</div>
            <div
              class="menu-item-menu-contact"
              v-for="(item,i) in menuListLeft"
              :key="i"
              @click="getThreeData(item.id)"
            >{{item.name}}</div>
          </div>
          <div class="menu-item-list">
            <div class="menu-item-list-item" v-for="(item,i) in menuListRight" :key="i">
              <div class="menu-item-list-title">{{item.name}}</div>
              <div
                class="menu-item-list-contact"
                v-for="(item1,i1) in item.children"
                :key="i1"
                @click="gotoLinks(item1.url,item1.id)"
              >{{item1.name}}</div>
            </div>
          </div>
        </div>

        <!-- </div> -->
      </div>
      <div class="menu-login">
        <el-link :underline="false" @click="applyJoin">申请入驻</el-link>
        <el-link :underline="false">控制台</el-link>
        <el-link :underline="false" @click="loginName()">{{names}}</el-link>
        <el-link :underline="false" v-show="shows" @click="outName()">退出</el-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      imgLogo: require('../assets/logo.png'),
      tableData: [],
      menuListLeft: [],
      menuListRight: [],
      show: false,
      shows: false,
      twoName: '',
      names: '登录',
      index: sessionStorage.getItem('index')
        ? sessionStorage.getItem('index')
        : 0,
      index1: 0,
    }
  },
  created() {
    this.fetch()
    this.shua()
  },
  watch: {
    $route: 'shua',
  },
  methods: {
    // 获取一级导航
    async fetch() {
      const res = await this.$http.post('/getallmenus')
      this.tableData = res.data.data
    },
    // 获取二级菜单
    async getTwoList(i, j, k) {
      const res = await this.$http.post('/getSsubordinateMenu', { ids: i })
      this.index1 = k
      if (res.data.data) {
        this.show = true
        this.menuListLeft = res.data.data.dataone
        this.menuListRight = res.data.data.datatwo
        this.twoName = j
      } else {
        this.show = false
        this.menuListLeft = []
        this.menuListRight = []
      }
    },
    // 鼠标滑入左边内容请求三级菜单
    async getThreeData(i) {
      const res = await this.$http.post('/getRightMenu', { ids: i })
      if (res.data.data) {
        this.menuListRight = res.data.data
      }
    },
    // 点击导航按钮
    async gotoLink(i, j, k) {
      if (i == 'serviceList' || i == 'release') {
        if (sessionStorage.getItem('codes')) {
          const res = await this.$http.post('/yanzhengUser', {
            codes: sessionStorage.getItem('codes'),
          })
          if (res.data.code == 0) {
            sessionStorage.setItem('index', k)
            this.index = k
            this.show = false
            this.$router.push({
              name: i,
              params: {
                //params 传递参数
                ids: j,
              },
            })
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning',
            })
            this.$router.push('/login')
          }
        } else {
          this.$router.push('/login')
        }
      } else {
        this.index = k
        sessionStorage.setItem('index', k)
        this.show = false
        this.$router.push({
          name: i,
          params: {
            //params 传递参数
            ids: j,
          },
        })
      }
      sessionStorage.setItem('url', i)
    },
    async gotoLinks(i, j) {
      if (i == 'serviceList' || i == 'release') {
        if (sessionStorage.getItem('codes')) {
          const res = await this.$http.post('/yanzhengUser', {
            codes: sessionStorage.getItem('codes'),
          })
          if (res.data.code == 0) {
            this.index = this.index1
            sessionStorage.setItem('index', this.index1)
            this.show = false
            this.$router.push({
              name: i,
              params: {
                //params 传递参数
                ids: j,
              },
            })
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning',
            })
            this.$router.push('/login')
          }
        } else {
          this.$router.push('/login')
        }
      } else {
        this.index = this.index1
        sessionStorage.setItem('index', this.index1)
        this.show = false
        this.$router.push({
          name: i,
          params: {
            //params 传递参数
            ids: j,
          },
        })
      }
      sessionStorage.setItem('url', i)
    },
    // 申请入驻
    applyJoin() {
      this.$router.push('/applyJoin')
    },

    loginName() {
      if (sessionStorage.getItem('names')) {
        this.names = sessionStorage.getItem('names')
        this.shows = true
      } else {
        this.$router.push({
          name: 'login',
        })
      }
    },
    shua() {
      if (sessionStorage.getItem('names')) {
        this.names = sessionStorage.getItem('names')
        this.shows = true
      }
    },

    outName() {
      sessionStorage.clear()
      this.names = '登录'
      this.shows = false
    },
  },
  mounted() {},
}
</script>
