<template>
  <!-- <slider @end="loadData" v-loading="loading"> -->
  <slider style="margin-top:10px;background-color: #fff;">
    <div
      class="my-scroll-item"
      v-for="(item, index) in scrollList"
      :key="index"
      @click="itemClick(item, index)"
    >
      <div class="timeImg">
        <img :src="item.cover_photo" alt srcset />
      </div>
    </div>
  </slider>
</template>
<script>
import Slider from '../components/SliderStep'

export default {
  name: 'better_scroll',
  components: { Slider },
  data() {
    return {
      scrollList: [],
      loading: false,
    }
  },
  created() {
    this.getScrollList()
  },
  mounted() {},
  methods: {
    async getScrollList() {
      const res = await this.$http.post('/getPartner')
      this.scrollList = res.data.data
    },
  },
}
</script>
<style scoped lang="less">
.my-scroll-item {
  width: 200px;
}
.timeImg {
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;
  img {
    width: 180px;
  }
}
</style>