<template>
  <div id="app" class="app">
    <el-container>
      <el-header v-show="$route.name!=='login' && $route.name!=='applyJoin'">
        <Header />
      </el-header>
      <el-main style="padding:0">
        <transition :name="transitionName" mode="out-in">
          <router-view></router-view>
        </transition>
      </el-main>
    </el-container>
    <el-backtop></el-backtop>
  </div>
</template>

<script>
// 引入formatDate.js 文件
import Header from './components/Header.vue'
import Footer from './components/Footer.vue'
export default {
  name: 'app',
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      transitionName: 'slide-right', //初始过度动画方向
    }
  },
  watch: {
    $route(to, from) {
      //切换动画
      let isBack = this.$router.isBack //监听路由是前进还是后退
      if (isBack) {
        this.transitionName = 'slide-right'
      } else {
        this.transitionName = 'slide-left'
      }
      this.$router.isBack = false
    },
  },
}
</script>

